@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}
.app-container {
  background-color: #f1f4f8;
  width: 100%;
}
.card-title svg {
  font-size: 2em;
}

.header {
  grid-column: 1;
  grid-row: 1;
  place-self: center center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 16px;
}
.table-wrapper {
  grid-column: 1;
  grid-row: 4;
  height: 100%;
  max-width: 100%;
  padding: 24px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.map {
  grid-column: 1;
  grid-row: 2;
}
.map-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.wrapper {
  width: 100%;
  height: 100%;
  min-height: 40vh;
  padding: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background: #f1f3f9;
}
svg {
  width: 100%;
  flex: 1;
  overflow: auto;
  position: relative;
}
svg .department:hover {
  stroke: black;
  cursor: pointer;
}
svg .label {
  position: absolute;
  z-index: 100;
  top: 0;
  right: 0;
  background-color: white;
  color: black;
}
@media screen and (min-width: 800px) {
  .app-wrapper {
    padding: 24px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto 1fr;
  }
  .map {
    grid-column: 2;
    grid-row: 2;
  }
  .table-wrapper {
    grid-row: 2;
  }
}
